<template>
  <div class="products">
    <Loading :active.sync="isLoading" />

    <div class="text-right mt-4 mx-5">
      <button class="btn btn-primary" @click="openModal('new')">
        
      </button>
    </div>

    <div class="d-flex align-items-center my-4">
        <table
        class="table table-striped table-bordered" cellspacing="0" width="100%">
          <thead>
            <tr>
              <th width="100">
                
              </th>
              <th></th>
              <th width="80">
                
              </th>
              <th width="80">
                
              </th>
              <th width="120">
                
              </th>
              <th width="200">
                
              </th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="item in products" :key="item.id">
              <td>{{ item.category }}</td>
              <td>{{ item.title }}</td>
              <td class="text-right">
                {{ item.origin_price | currency }}
              </td>
              <td class="text-right">
                {{ item.price | currency }}
              </td>
              <td>
                <span v-if="item.enabled" class="text-success"></span>
                <span v-else></span>
              </td>
              <td>
                <div class="btn-group">
                  <button class="btn btn-outline-primary btn-sm" @click="openModal('edit', item)">
                    
                  </button>
                  <button class="btn btn-outline-danger btn-sm" @click="openModal('delete', item)">
                    
                  </button>
                </div>
              </td>
            </tr>
          </tbody>
        </table>
    </div>
    <Pagination
      :pages="pagination"
      @naviPages="getProducts"
    />

    <!-- Modal (Show Product) START-->
    <div
      id="productModal"
      class="modal fade"
      tabindex="-1"
      role="dialog"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog modal-xl" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-dark text-white">
            <h5 id="exampleModalLabel" class="modal-title">
              <span v-if="isNew"></span>
              <span v-else> {{ tempProduct.title }} </span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>

          <div class="modal-body">
            <div class="row">
              <div class="col-sm-4">
                <div v-for="i in 5" :key="i + 'img'" class="form-group">
                  <label :for="'img' + i"> {{ i }}</label>
                  <input
                    :id="'img' + i"
                    v-model="tempProduct.imageUrl[i - 1]"
                    type="text"
                    class="form-control"
                    placeholder=""
                  />
                </div>

                <div class="form-group">
                  <label for="customFile">
                    
                    <i v-if="status.fileUploading" class="fas fa-spinner fa-spin" />
                  </label>
                  <input
                    id="customFile"
                    ref="file"
                    type="file"
                    class="form-control"
                    @change="uploadFile"
                  />
                </div>
                <img class="img-fluid" :src="tempProduct.imageUrl[0]" alt />
              </div>

              <div class="col-sm-8">
                <div class="form-group">
                  <ValidationProvider name="" rules="required|min:4"  v-slot="{ errors }">
                      <label class="d-flex" for="title" ></label>
                      <input
                        id="title"
                        v-model="tempProduct.title"
                        type="text"
                        class="form-control"
                        placeholder=""
                        required
                      />
                     <small id="titleErr" class="form-text d-flex text-danger">
                       {{ errors[0] }}
                     </small>
                  </ValidationProvider>
                </div>

                <div class="form-row">
                  <div class="form-group col-md-6">
                    <ValidationProvider name="" rules="required|min:4"  v-slot="{ errors }">
                    <label class="d-flex" for="category"></label>
                    <input
                      id="category"
                      v-model="tempProduct.category"
                      type="text"
                      class="form-control"
                      placeholder=""
                      required
                    />
                    <small id="categoryErr" class="form-text d-flex text-danger">
                       {{ errors[0] }}
                    </small>
                    </ValidationProvider>
                  </div>

                  <div class="form-group col-md-6">
                    <ValidationProvider name="" rules="required|min:2"  v-slot="{ errors }">
                    <label class="d-flex" for="unit"></label>
                    <input
                      id="unit"
                      v-model="tempProduct.unit"
                      type="text"
                      class="form-control"
                      placeholder=""
                    />
                    <small id="unitErr" class="form-text d-flex text-danger">
                       {{ errors[0] }}
                    </small>
                    </ValidationProvider>
                  </div>
                </div>

                <div class="form-row">
                  <div class="form-group col-md-6">
                    <ValidationProvider name="" rules="required"  v-slot="{ errors }">
                    <label class="d-flex" for="origin_price"></label>
                    <input
                      id="origin_price"
                      v-model="tempProduct.origin_price"
                      type="number"
                      class="form-control"
                      placeholder=""
                    />
                    <small id="origin_priceErr" class="form-text d-flex text-danger">
                       {{ errors[0] }}
                    </small>
                    </ValidationProvider>
                  </div>

                  <div class="form-group col-md-6">
                  <ValidationProvider name="" rules="required" v-slot="{ errors }">
                    <label class="d-flex" for="price"></label>
                    <input
                      id="price"
                      v-model="tempProduct.price"
                      type="number"
                      class="form-control"
                      placeholder=""
                    />
                    <small id="priceErr" class="form-text d-flex text-danger">
                       {{ errors[0] }}
                    </small>
                    </ValidationProvider>
                  </div>
                </div>

                <div class="form-group">
                  <ValidationProvider name="" rules="required|min:10"  v-slot="{ errors }">
                  <label class="d-flex" for="description"></label>
                  <textarea
                    id="description"
                    v-model="tempProduct.description"
                    type="text"
                    class="form-control"
                    placeholder=""
                    required
                  />
                  <small id="descriptionErr" class="form-text d-flex text-danger">
                    {{ errors[0] }}
                  </small>
                  </ValidationProvider>
                </div>

                <div class="form-group">
                  <ValidationProvider name="" rules="required|min:6"  v-slot="{ errors }">
                  <label class="d-flex" for="content"></label>
                  <vue-editor
                    id="content"
                    v-model="tempProduct.content"
                  />
                  <small id="contentErr" class="form-text d-flex text-danger">
                    {{ errors[0] }}
                  </small>
                  </ValidationProvider>
                </div>

                <div class="form-group">
                  <div class="form-check">
                    <input
                      class="form-check-input d-flex"
                      id="is_enabled"
                      v-model="tempProduct.enabled"
                      type="checkbox"
                    />
                    <label class="form-check-label d-flex" for="is_enabled"></label>
                  </div>
                </div>
              </div>
            </div>
          </div>

          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal">
              
            </button>

            <button type="button" class="btn btn-primary" @click="updateProduct">
              
            </button>
          </div>
        </div>
      </div>
    </div>
    <!-- Modal (Show Product) ENDOF-->

    <!-- Modal (Delete Product) START-->
    <div
      id="delProductModal"
      class="modal fade"
      tabindex="-1"
      role="dialog"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-warning text-white">
            <h5 id="exampleModalLabel" class="modal-title">
              <span></span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            
            <strong class="text-danger">{{ tempProduct.title }}</strong>
            ()
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal">
              
            </button>
            <button type="button" class="btn btn-warning" @click="delProduct">
              
            </button>
          </div>
        </div>
      </div>
    </div>
    <!-- Modal (Delete Product) ENDOF-->
  </div>
</template>

<script>
import { VueEditor } from 'vue2-editor';
import Pagination from '@/components/Pagination.vue';

export default {
  name: 'Products',
  components: {
    VueEditor,
    Pagination,
  },
  data() {
    return {
      products: [],
      pagination: {},
      tempProduct: {
        imageUrl: [],
      },
      isNew: false,
      isLoading: false,
      status: {
        fileUploading: false,
      },
      uuid: process.env.VUE_APP_UUID,
      xerror: 'NO_ERROR',
    };
  },
  created() {
    this.token = document.cookie.replace(/(?:(?:^|.*;\s*)BruceStoreT7_token\s*=\s*([^;]*).*$)|^.*$/, '$1');
    const url = `${process.env.VUE_APP_APIPATH}auth/check`;
    // Axios 
    this.$http.defaults.headers.common.Authorization = `Bearer ${this.token}`;
    this.$http
      .post(url, { api_token: this.token })
      .then(() => {
        this.getProducts();
      })
      .catch((err) => {
        this.$swal.fire({
          icon: 'error',
          title: 'Oops0...',
          text: `${err.request.status}`,
        });
      });
  },
  methods: {
    getProducts(page = 1) {
      this.isLoading = true;
      const api = `${process.env.VUE_APP_APIPATH}${this.uuid}/admin/ec/products`;
      this.$http
        .get(api,
          {
            params: {
              page: `${page}`,
              paged: `${process.env.VUE_APP_PAGED}`,
            },
          })
        .then((res) => {
          this.products = res.data.data;
          this.pagination = res.data.meta.pagination;
          this.isLoading = false;
        })
        .catch((error) => {
          this.$swal.fire({
            icon: 'error',
            title: 'Oops1...',
            text: `${error.request.status}`,
          });
          this.isLoading = false;
        });
    },

    // openModal
    openModal(isNew, item) {
      switch (isNew) {
        case 'new':
          this.tempProduct = {
            imageUrl: [],
          };
          this.isNew = true;
          $('#productModal').modal('show');
          break;
        case 'edit':
          this.isNew = false;
          this.getDetails(item.id);
          break;
        case 'delete':
          this.tempProduct = { ...item };
          $('#delProductModal').modal('show');
          break;
        default:
          break;
      }
    },
    delProduct() {
      this.isLoading = true;
      const url = `${process.env.VUE_APP_APIPATH}${this.uuid}/admin/ec/product/${this.tempProduct.id}`;
      this.$http.delete(url)
        .then(() => {
          $('#delProductModal').modal('hide');
          this.isLoading = true;
          this.$swal.fire({
            icon: 'success',
            title: ` ${this.tempProduct.title}`,
            text: ' !',
            timer: 1500,
          });
          this.getProducts(this.pagination.current_page);
        })
        .catch((error) => {
          this.isLoading = false;
          this.$swal.fire({
            icon: 'error',
            title: ` ${this.tempProduct.title}`,
            text: `${error.request.status}`,
          });
        });
      this.getProducts(this.pagination.current_page);
    },
    uploadFile() {
      const uploadedFile = this.$refs.file.files[0];
      const formData = new FormData();
      formData.append('file', uploadedFile);
      const url = `${process.env.VUE_APP_APIPATH}${process.env.VUE_APP_UID}/admin/storage`;

      this.status.fileUploading = true;
      this.$http.post(url, formData, {
        headers: {
          'Content-Type': 'multipart/form-data',
        },
      }).then((response) => {
        this.status.fileUploading = false;
        if (response.status === 200) {
          this.tempProduct.imageUrl.push(response.data.data.path);
        }
      }).catch((error) => {
        this.status.fileUploading = false;
        this.$swal.fire({
          icon: 'error',
          title: 'Oops2...',
          text: ` 2MB ${error.request.status}`,
        });
      });
    },
    updateProduct() {
      // 
      let api = `${process.env.VUE_APP_APIPATH}${this.uuid}/admin/ec/product`;
      let httpMethod = 'post';

      //  API
      if (!this.isNew) {
        api = `${process.env.VUE_APP_APIPATH}${this.uuid}/admin/ec/product/${this.tempProduct.id}`;
        httpMethod = 'patch';
      }

      this.$http[httpMethod](api, this.tempProduct).then(() => {
        $('#productModal').modal('hide');
        this.$swal.fire({
          icon: 'success',
          title: `${this.isNew ? '' : ''} `,
          text: '!',
          timer: 1500,
        });
        this.isLoading = false;
        this.getProducts(this.pagination.current_page);
      }).catch((error) => {
        this.xerror = error.response.data;
        this.isLoading = false;
        $('#productModal').modal('hide');
        this.$swal.fire({
          icon: 'error',
          title: `${this.isNew ? '' : ''} `,
          text: '!',
          timer: 1500,
        });
      });
    },
    getDetails(id) {
      this.isLoading = true;
      const api = `${process.env.VUE_APP_APIPATH}${this.uuid}/admin/ec/product/${id}`;
      this.$http.get(api)
        .then((res) => {
          this.tempProduct = res.data.data;
          $('#productModal').modal('show');
          this.isLoading = false;
        })
        .catch((error) => {
          this.$swal.fire({
            icon: 'error',
            title: 'Oops3...',
            text: `${error.request.status}`,
          });
          this.isLoading = false;
        });
    },
  },
};
</script>
